import moment from "moment";

export default {
  props: ["time", "status"],
  data() {
    return {
      modelConfig: {
        type: "string",
        mask: "DD.MM.YYYY HH:mm:00",
      },
      start: moment().subtract(6, "hour").format("DD.MM.YYYY HH:mm:00"),
      end: moment().format("DD.MM.YYYY HH:mm:00"),
      speed: 3600,
      interval: 1000,
      sensors: "",
      win: true,
      maxPeriod: 1,
    };
  },
  computed: {
    timeFormat: function () {
      return moment(this.time, "X").format("DD.MM.YYYY HH:mm:ss");
    },
    startTimestamp: function () {
      return Number(moment(this.start, "DD.MM.YYYY HH:mm:ss").format("X"));
    },
    endTimestamp: function () {
      return Number(moment(this.end, "DD.MM.YYYY HH:mm:ss").format("X"));
    },
    // maxEnd: function () {
    //   const end = moment(this.start, "DD.MM.YYYY HH:mm:ss").add(1, "day");
    //   const today = moment();
    //   if (end > today) {
    //     return Number(today.format("x"));
    //   }
    //   return Number(end.format("x"));
    // },
  },
  watch: {
    start() {
      const start = moment(this.start, "DD.MM.YYYY HH:mm:ss");
      const end = moment(this.end, "DD.MM.YYYY HH:mm:ss");
      if (start > end) {
        this.end = start.add(1, "hour").format("DD.MM.YYYY HH:mm:00");
      } else if (end.diff(start, "days") > this.maxPeriod) {
        this.end = start
          .add(this.maxPeriod, "day")
          .format("DD.MM.YYYY HH:mm:00");
      }
    },
    end() {
      const start = moment(this.start, "DD.MM.YYYY HH:mm:ss");
      const end = moment(this.end, "DD.MM.YYYY HH:mm:ss");
      if (end < start) {
        this.start = end.subtract(1, "hour").format("DD.MM.YYYY HH:mm:00");
      } else if (end.diff(start, "days") > this.maxPeriod) {
        this.start = end
          .subtract(this.maxPeriod, "day")
          .format("DD.MM.YYYY HH:mm:00");
      }
    },
  },
  methods: {
    run() {
      this.$emit("start", {
        start: this.startTimestamp,
        end: this.endTimestamp,
        speed: Number(this.speed),
        interval: Number(this.interval),
        sensors: this.sensors,
      });
    },
    stop() {
      this.$emit("stop");
    },
    pause() {
      this.$emit("pause");
    },
    play() {
      this.$emit("play");
    },
    minimize() {
      this.win = !this.win;
    },
  },
};
